var emailAddress = ['@163.com', '@126.com', '@qq.com', '@sina.com', '@hotmail.com', '@sohu.com', '@gmail.com', '@139.com', '@vip.163.com', '@yeah.net'];
/**
 * 邮箱脱敏 xxx****@xx.com
 * @param email 邮箱
 */

export var emailDesensitization = function emailDesensitization(email) {
  return email = email.replace(/^(.{3})(.*)(@.*)$/, '$1****$3');
};
/**
 * 获取该邮箱网站地址
 * @param email 邮箱
 */

export var loginEmail = function loginEmail(email) {
  return siteMap.get(email.split('@')[1]);
};
export var emailSearch = function emailSearch(query, cb) {
  var queryList = query.split('@');
  var result = query ? jointAddress(queryList) : [];
  cb(result);
};

var jointAddress = function jointAddress(queryList) {
  if (queryList.length < 2) {
    return emailAddress.map(function (item) {
      return {
        value: queryList[0] + item
      };
    });
  } else {
    return emailAddress.filter(function (item) {
      return item.includes(queryList[1]);
    }).map(function (cur) {
      return {
        value: queryList[0] + cur
      };
    });
  }
};