























































import _ from 'lodash';
import { getArrayObjIndexByKey, isEmpty } from "@/utils";
import {FormatMixin} from '@/assets/mixin'
import { Component, Vue, Mixins } from 'vue-property-decorator';
@Component({
  components: {},
})
export default class ViewComponent extends Mixins(FormatMixin) {
  /**data */
  selectedLabel = '全部'
  tabs: any = [
    {
      label: '全部',
      key: '2;3;4;5',
    },
    {
      label: '还款中',
      key: '4',
    },
    {
      label: '还款完成',
      key: '5',
    }
  ]
  items = [] // items重新赋值可以自动触发，拉动复位事件（refresh+forceUpdate+过渡动画）
  /**computed */
  get initialIndex() {
    return getArrayObjIndexByKey(this.tabs, { label: this.selectedLabel })
  }
  get options() {
    return {
      pullDownRefresh: {
        threshold: 60,
        stop: 40,
        txt: '更新成功'
      },
      pullUpLoad: {},
      scrollbar: true,
    }
  }

  /**life hook */
  created() {
    this.changePage(this.initialIndex);
  }

  /**method */
  async changePage(current: any, method: any = 'get') {
    this.selectedLabel = this.tabs[current].label
    // // console.log('当前轮播图序号为:' + current)
    const index = current;
    const item: any = this.tabs[index];
    const list: any = item ? (isEmpty(item.list) ? [] : item.list) : [];
    const scroll: any = this.$refs.cubeScroll ? (this.$refs.cubeScroll as any)[index] : {};
    if (method === 'get') { // 切换tab获取
      if (item.page) { // 已激活过，直接跳出
        return
      }
    }

    // 分页数据处理
    let page = 1;
    if (method === 'put') {
      // page = 1
    } else if (method === 'patch') {
      const currentPage = item.page.currentPage
      const totalPages = item.page.totalPages
      if (currentPage < totalPages) {
        page = currentPage + 1
      } else {
        this.$createToast({
          type: 'warn',
          txt: `无更多数据`,
          time: 1500,
        }).show()
        console.log('scroll:', scroll)
        setTimeout(() => {
          console.log('scroll after:', scroll)
          scroll && scroll.forceUpdate();
        }, 1000);
        return;
      }
    }

    // 发请求
    const status = item.key
    const data = await this.$request({
      api: 'userInvestRecord',
      params: {
        token: this.$store.state.token,
        status,
        page,
        size: 10,
      }
    })
    
    this.items = []
    if (data.resCode === '0000') {
      item.page = data.page
      const list = data.list
      if (list.length === 0) return;
      if (method === 'get') {
        
        this.tabs[index].list = list;
        console.log('index',index);
        console.log('this.tabs[index].list',this.tabs[index].list);
      }
      else if (method === 'put') {
        this.tabs[index].list = list;
      }
      else if (method === 'patch') {
        const clone: any = _.cloneDeep(item.list);
        item.list = clone.concat(list);
      }
    }
  }
  scroll(pos: any) {
    const x = Math.abs(pos.x)
    const tabItemWidth = (this.$refs.tabNav as any).$el.clientWidth
    const slideScrollerWidth = (this.$refs.slide as any).slide.scrollerWidth;
    const deltaX = x / slideScrollerWidth * tabItemWidth;
    (this.$refs.tabNav as any).setSliderTransform(deltaX)
  }
  onPullingDown() {
    // 模拟更新数据
    setTimeout(() => {
      this.changePage(this.initialIndex, 'put')
    }, 1000)
  }
  onPullingUp() {
    // 模拟更新数据
    setTimeout(() => {
      this.changePage(this.initialIndex, 'patch')
    }, 1000)
  }
}
